import styled from 'styled-components';

const Button = styled.button`
	height: 30px;
	width: 100px;
	color: white;
	background-color: #F09000;
	border: 5px solid #F09000;
	border-radius: 4px;
	font-size: 16px
	font-family: Helvetica, Arial, sans-serif;
	cursor: pointer;
`;

export default Button;


